

@media all and (min-width:0px) and (max-width: 450px) {
    #lan-main-div{
        color: white;
        display:flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        min-height: 100vh;
        min-width: 100vw;
        background-color: #97C3E7;
    }
    
    #lan-info-span{
        width:80%;
        font-size: 4vw;
    }
    
    #lan-buttons-div{
        width:100%; 
        display:flex;
        flex-direction: row;
        justify-content: space-evenly;
        align-items: center;
    }
    
    .lan-direction-button{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height:15vh;
        width:15vh;
        background-color:white;
        border-radius:100px;
        box-shadow: 0px 2px 2px grey;
    }
    
    
    .lan-direction-button:active{
        transform: scale(.95,.95);
    }
    
    #lan-provider-button{
        background: url('../../presets/icons/cesta.svg') no-repeat white;
        background-position: center;
        background-size: 60%;
    }
    
    
    #lan-receiver-button{
        background: url('../../presets/icons/comer.svg') no-repeat white;
        background-position: center;
        background-size: 60%;
    }
    
    .lan-button-label{
        text-align: center;
        padding-top: 23vh;
        font-size: 4vw;
        width:200%;
    }

    #lan-overlay-div{
        position: absolute;
        top:0px;
        left: 0px;
        height: 100vh;
        width:100vw;
        background-color: #97C3E7;
        
    }
  }

